import { Chip, Typography } from "@mui/material";
import { useState } from "react";

function CustomChip({
  label,
  icon,
  variant,
  color,
  size = "small",
  fontWeight = "bold",
}) {
  const [chipSize, setChipSize] = useState(size);
  const handleEnter = () => {
    setChipSize("medium");
  };
  const handleClose = () => {
    setChipSize("small");
  };
  return (
    <Typography
      sx={{ display: "inline-block" }}
      onMouseEnter={handleEnter}
      onMouseLeave={handleClose}
    >
      <Chip
        sx={{
          marginRight: "8px",
          marginBottom: "12px",
          fontWeight: fontWeight,
          background: "var(--chip-color)",
          border: "2px solid var(--color8)",
        }}
        label={label}
        icon={icon}
        variant={variant}
        color={color}
        size={chipSize}
      />
    </Typography>
  );
}

export default CustomChip;
